/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */

import React, { forwardRef, Fragment, useEffect, useRef, useState } from 'react';
import { bool, func, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-radio';
const CSS_STATE = 'vds-state';

const Radio = forwardRef(({
  className,
  disabled,
  id = shortid.generate(),
  invalid,
  label,
  onBlur,
  onChange,
  onFocus,
  ...remainingProps
}, ref) => {
  const [focused, setFocused] = useState();
  const [check, setCheck] = useState(remainingProps.checked || remainingProps.defaultChecked);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    if (typeof remainingProps.defaultChecked !== 'undefined') {
      setCheck(remainingProps.defaultChecked);
    }
  }, [remainingProps.defaultChecked]);

  useEffect(() => {
    if (typeof remainingProps.checked !== 'undefined') {
      setCheck(remainingProps.checked);
    }
  }, [remainingProps.checked]);

  const handleChange = event => {
    setCheck(event.target.checked);
    if (typeof onChange === 'function') {
      onChange(event);
    }
  };

  const handleFocus = event => {
    setFocused(true);
    if (typeof onFocus === 'function') {
      onFocus(event);
    }
  };

  const handleBlur = event => {
    setFocused(false);
    if (typeof onBlur === 'function') {
      onBlur(event);
    }
  };

  return (
    <Fragment>
      <input
        className={classnames(CSS_PREFIX, {
          'focus-visible': Boolean(focused)
        }, className)}
        disabled={disabled}
        id={uid}
        onBlur={handleBlur}
        onChange={handleChange}
        onFocus={handleFocus}
        type="radio"
        ref={ref}
        {...remainingProps}
      />
      <label
        className={classnames(
          `${CSS_PREFIX}-label`,
          {
            [`${CSS_STATE}--checked`]: Boolean(check),
            [`${CSS_STATE}--disabled`]: Boolean(disabled),
            [`${CSS_STATE}--focus`]: Boolean(focused),
            [`${CSS_STATE}--invalid`]: Boolean(invalid)
          }
        )}
        htmlFor={uid}
        onClick={e => e.stopPropagation()} // if not event fires 2x, one for radio and one for label
        onKeyPress={e  => e.stopPropagation()} // if not key-press event fires 2x, one for checkbox and one for label
      >
        {label}
      </label>
    </Fragment>
  );
});

Radio.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   *   If `true`, the radio will be disabled.
   */
  disabled: bool,

  /**
   * The id of the input element.
   */
  id: oneOfType([number, string]),

  /**
   * Invalid
   */
  invalid: bool,

  /**
   *   The label of the radio element.
   */
  label: string.isRequired,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input element value changes.
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func
};

Radio.displayName = 'Radio';

export default Radio;
