import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-radio-error-message';

const RadioErrorMessage = ({ ariaAtomic, ariaLive, children, className, ...remainingProps }) => (
  <span
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

RadioErrorMessage.propTypes = {
  /**
   * Aria Atomic
   */
  ariaAtomic: string,

  /**
   * Aria Live
   */
  ariaLive: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

RadioErrorMessage.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'assertive'
};

RadioErrorMessage.displayName = 'RadioErrorMessage';

export default RadioErrorMessage;
