import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-radio-list';

const RadioList = ({ children, className, orientation, ...remainingProps }) => (
  <ul
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation)
    }, className)}
    {...remainingProps}
  >
    {children}
  </ul>
);

RadioList.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * orientation
   */
  orientation: oneOf(['horizontal', 'vertical'])
};

RadioList.displayName = 'RadioList';

export default RadioList;
