import React, { forwardRef } from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import ButtonLabel from '../button-label';

const CSS_PREFIX = 'vds-btn-text';
const CSS_STATE = 'vds-state';

const ButtonText = forwardRef(({
  active,
  children,
  className,
  colorScheme,
  disabled,
  flippable,
  isFullWidth,
  role,
  tag: Tag,
  type,
  ...remainingProps
}, ref) => (
  <Tag
    className={classnames({
      [`${CSS_PREFIX}`]: Boolean(colorScheme) === false,
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_PREFIX}--${colorScheme}`]: Boolean(colorScheme),
      [`${CSS_STATE}--flippable`]: Boolean(flippable),
      [`${CSS_PREFIX}--fullwidth`]: Boolean(isFullWidth),
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className)}
    disabled={disabled}
    role={Tag === 'button' ? undefined : role}
    type={Tag === 'button' ? type : undefined}
    ref={ref}
    {...remainingProps}
  >
    <ButtonLabel>{children}</ButtonLabel>
  </Tag>
));

ButtonText.propTypes = {
  /**
   * Active
   */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Color scheme of button
   */
  colorScheme: oneOf([
    'primary',
    'secondary',
    'tertiary'
  ]),

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Flippable
   */
  flippable: bool,

  /**
   * Full width
   */
  isFullWidth: bool,

  /**
   * @ignore
   */
  role: string,

  /**
   * Tag of button
   */
  tag: oneOf([
    'a',
    'button',
    'div',
    'span'
  ]),

  /**
   * @ignore
   */
  type: string
};

ButtonText.defaultProps = {
  role: 'button',
  tag: 'button',
  type: 'button'
};

ButtonText.displayName = 'ButtonText';

export default ButtonText;
