import React, { cloneElement, forwardRef } from 'react';
import { bool, element, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-link';
const CSS_STATE = 'vds-state';

const Link = forwardRef(({
  className,
  disabled,
  inline,
  tag,
  ...remainingProps
}, ref) => (
  cloneElement(tag, {
    className: classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--inline`]: Boolean(inline),
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className),
    ref,
    ...remainingProps
  })
));

Link.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Disabled
  */
  disabled: bool,

  /**
  * Inline
  */
  inline: bool,

  /**
  * Tag
  */
  tag: element
};

Link.defaultProps = {
  tag: <a /> // eslint-disable-line
};

Link.displayName = 'Link';

export default Link;
