import React, { Children, cloneElement, Fragment } from 'react';
import { bool, func, node, string } from 'prop-types';
import RadioGroup from '../radio-group';
import RadioList from '../radio-list';
import RadioListItem from '../radio-list-item';
import RadioGroupLegend from '../radio-group-legend';
import RadioErrorMessage from '../radio-error-message';
import Icon from '../icon';

const getChild = ({ child, disabled, index, invalid, value }) =>
  cloneElement(child, {
    checked: child.props.value === value,
    disabled: child.props.disabled || disabled,
    invalid: child.props.invalid || invalid,
    key: index
  });

const RadioGroupControl = ({
  children,
  disabled,
  errorIcon,
  errorMessage,
  invalid,
  isVertical,
  legend,
  onChange,
  value,
  ...remainingProps
}) => (
  <RadioGroup
    disabled={disabled}
    invalid={invalid}
    onChange={onChange}
    {...remainingProps}
  >
    { legend ? <RadioGroupLegend>{legend}</RadioGroupLegend> : undefined }
    {
      isVertical
        ? <RadioList>
          {
            Children.map(children, (child, index) => (
              <RadioListItem key={index}>
                {
                  getChild({
                    child,
                    disabled,
                    index,
                    invalid,
                    value
                  })
                }
              </RadioListItem>
            ))
          }
        </RadioList>
        : <Fragment>
          {
            Children.map(children, (child, index) => (
              getChild({
                child,
                disabled,
                index,
                invalid,
                value
              }))
            )
          }
        </Fragment>
    }
    {
      invalid && errorMessage
        ? <RadioErrorMessage>{errorIcon} {errorMessage}</RadioErrorMessage>
        : undefined
    }
  </RadioGroup>
);

RadioGroupControl.propTypes = {
  /**
   * Content
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * If `true`, the radio will be disabled.
   */
  disabled: bool,

  /**
   * Error icon
   */
  errorIcon: node,

  /**
   * Error message
   */
  errorMessage: string,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Is Vertical
   */
  isVertical: bool,

  /**
   * Legend
   */
  legend: string,

  /**
   * Callback fired when a radio button is selected.
   * @param {object} event
   */
  onChange: func,

  /**
   * Value of the selected radio button.
   */
  value: string
};

RadioGroupControl.defaultProps = {
  errorIcon: <Icon name="error" resolution="tiny" />
};

RadioGroupControl.displayName = 'RadioGroupControl';

export default RadioGroupControl;
export { getChild };
