import React from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-radio-group';

const RadioGroup = ({ children, className, disabled, invalid, ...remainingProps }) => (
  <fieldset
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--disabled`]: Boolean(disabled),
      [`${CSS_PREFIX}--error-required`]: Boolean(invalid)
    }, className)}
    {...remainingProps}
  >
    {children}
  </fieldset>
);

RadioGroup.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Invalid
   */
  invalid: bool
};

RadioGroup.displayName = 'RadioGroup';

export default RadioGroup;
