import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-radio-group-legend';

const RadioGroupLegend = ({ children, className, ...remainingProps }) => (
  <legend
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </legend>
);

RadioGroupLegend.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

RadioGroupLegend.displayName = 'RadioGroupLegend';

export default RadioGroupLegend;
